.color__block__container {
    position: relative;
    height: 72px;
    width: 100%;
    -ms-flex-negative: 1;
        flex-shrink: 1;
    cursor: pointer;
}

.color__block__container.invalid {
    -webkit-box-shadow: inset 0 0 0 2px #DDD;
            box-shadow: inset 0 0 0 2px #DDD;
}

.color__block__container:not(:hover) .color__block__code {
    opacity: 0;
    -webkit-transition: 600ms;
    -o-transition: 600ms;
    transition: 600ms;
}

@media (max-width: 720px) {
    .color__block__container.wide {
        min-width: 144px;
    }
}

.color__block__code {
    position: absolute;
    top: 100%;
    padding-top: 8px;
    padding-bottom: 16px;
    -webkit-transition-duration: 200ms;
         -o-transition-duration: 200ms;
            transition-duration: 200ms;
}

@-webkit-keyframes copyAnimation {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    30% {
        opacity: 0.5;
    }
    70% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 0.3;
    }
    100% {
        opacity: 0;
    }
}

@keyframes copyAnimation {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    30% {
        opacity: 0.5;
    }
    70% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 0.3;
    }
    100% {
        opacity: 0;
    }
}

.copied__text {
    -webkit-animation: copyAnimation 800ms;
            animation: copyAnimation 800ms;
    opacity: 0;
}


